textarea[data-apidoc-data="yaml"] {
    display: none;
}

.gem-ig-api-doc {
    font-family: sans-serif;
    font-size: 12px;
}


.gem-ig-api-doc a {
    color: #000;
    border-bottom: 1px dashed rgba(0, 0, 0, 0.2);
    padding-bottom: 2px;
    text-decoration: none;
}

.gem-ig-api-doc a:hover {
    color: #0d7395;
}

.gem-ig-api-doc a:active {
    color: #000;
}

.gem-ig-api-doc p {
    padding: 0px;
}

.gem-ig-api-doc th {
    text-align: left;
    font-weight:bold;
    padding-right: 10px;
    padding-left: 5px;
}

.gem-ig-api-doc .operation-block {
    border: 1px solid #000;
    border-radius: 2px;
    margin: 0 0 15px;
}

.gem-ig-api-doc .operation-block-summary {
    all: inherit;
    border: 0;
    cursor:default;
    flex: 1;
    padding: 0;
    margin: 0;
    align-items: center;
    display: flex;
    box-shadow: 0 0 3px rgba(0, 0, 0, .19);
    border-bottom-left-radius: 0%;
    border-bottom-right-radius: 0%;
}

.operation-block-summary-control {
    display: flex;
}

.gem-ig-api-doc .operation-block-summary-method {
    border-radius: 2px;
    color: #fff;
    font-family: sans-serif;
    font-size: 12px;
    font-weight: 700;
    min-width: 60px;
    padding: 6px 0;
    margin: 5px;
    text-align: center;
    border-style: none;
