    font-family: monospace;
    font-size: 14px;
    font-weight: 600;
    word-break: break-word;
}

.gem-ig-api-doc .operation-block-description {
    padding: 10px 20px 10px 20px;
}

.gem-ig-api-doc .operation-block-description.low-padding {
    padding: 0px;
    padding-bottom: 10px;
    padding-left: 20px;
}

.gem-ig-api-doc .operation-block-description.with-table {
    overflow: scroll;
}

.gem-ig-api-doc .operation-block-description.operation-example {
    padding: 0px 20px;
}

.gem-ig-api-doc .operation-block-section-header {
    background-color: #ffffff;
    padding: 10px 20px;
    font-weight: bold;
}

.gem-ig-api-doc .example {
    border-radius: 2px;
    background-color: #ffffff;
    font-family: sans-serif;
    font-size: 12px;
    font-weight: 700;
    min-width: 60px;
    padding: 0px 10px 0px 0px;
    text-align: left;
    border: 1px solid;
    border-color: #519ad9;
    height: 30px;
    cursor: pointer;
    margin-bottom: 5px;
    max-width: 400px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

}

.gem-ig-api-doc .example .label {
    border-radius: 2px 0 0 2px;
    background-color: #519ad9;
    color: #fff;
    padding: 8px;
    margin-right: 10px;
    min-width: 60px;
    font-size: 100%;
}

.gem-ig-api-doc .inline-button {
    display: inline-block;
    margin-right: 10px;
}

.gem-ig-api-doc button:hover, .gem-ig-api-doc .active-button {
    opacity: 0.5;
}

.gem-ig-api-doc .example-buttons-container{
    padding: 3px;
    text-align: right;
}

.gem-ig-api-doc table {
    max-width: 100%;
}

.gem-ig-api-doc table.params-table tr th:nth-child(1) {
    width: 240px;
}

.gem-ig-api-doc table.params-table tr th:nth-child(2) {
    width: 120px;
}

.gem-ig-api-doc table.params-table tr th:nth-child(3) {
    width: 680px;
}

.gem-ig-api-doc table tr td {
    border-bottom: 1px solid rgba(192, 192, 192, 0.2);
    border-spacing: 0;
}

.gem-ig-api-doc table tr:last-child td {
    border-bottom: none;
}

.gem-ig-api-doc table tr:nth-child(even) {
    background: #eeeeee89;
}

.gem-ig-api-doc table tr:nth-child(odd) {
    background: #ffffff89;
}

.gem-ig-api-doc table th {
    background: rgba(var(--fhir-api-doc-color), 0.2);
}

.operation-block-description > pre {
    background-color: #171c19;
    color: #fff;
    padding: 10px;
}

.gem-ig-api-doc .operation-example > pre {
    background-color: #171c19;
    color: #fff;
    overflow: auto;
    white-space: pre;
    padding: 10px;
}

.gem-ig-api-doc code {
    color: inherit;
}

.gem-ig-api-doc .operation-example > pre > .html-example {
    overflow: auto;
    padding: 10px;
    color: black;
    background: white;

}

